import React from "react";

function fun() {
  const cd = new Date();

  console.log(cd);
  const hr = cd.getHours();
  let dayhr;
  let daycolor = {
    color: ""
  };

  if (hr < 12) {
    dayhr = "Good Morning";
    daycolor.color = "red";
  } else if (hr >= 12 && hr < 18) {
    dayhr = "Good Afternoon";
    daycolor.color = "orange";
  } else if (hr >= 18 && hr < 21) {
    dayhr = "Good Evening";
    daycolor.color = "pink";
  } else {
    dayhr = "Good Nignt";
    daycolor.color = "blue";
  }

  return <h1 style={daycolor}>{dayhr}</h1>;
}

export default fun;
